.key {
  width: 43px;
  height: 58px;
  background: #818384;
  display: inline-block;
  border: none;
  border-radius: 6px;
  line-height: 50px;
  cursor: pointer;
  color: #fff;
  font-weight: bold;
  
  &--big {
    width: 65px;
  }

  &--green {
    background: #538d4e;
    color: #fff;
    transition: all 0.3s ease-in;
  }

  &--yellow {
    background: #b59f3b;
    color: #fff;
    transition: all 0.3s ease-in;
  }

  &--grey {
    background: #3a3a3c;
    color: #fff;
    transition: all 0.3s ease-in;
  }
}